new Swiper('.movies-for-the-future__swiper', {
  navigation: {
    nextEl: '.movies-for-the-future__swiper .movies-swiper-btn-prev',
    prevEl: '.movies-for-the-future__swiper .movies-swiper-btn-next',
  },
  keyboard: {
    enabled: true,
    onlyInViewport: true,
  },
  slidesPerView: 6,
  spaceBetween: 16,
  loop: true,
  autoplay: {
    delay: 5000,
    disableOnInteraction: false
  },
  speed: 800,
  autoHeight: true,
});

